/* Transactions page styling */

/* Transaction container and table */
.transactions-container {
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
  background-color: #fff;
  margin: 0 auto;
  width: 90%;
  max-width: 1000px;
  margin-right: auto;
  margin-left: 0%;
}

[data-theme="dark"] .transactions-container {
  background-color: #1e1e2d;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.transactions-table {
  width: 100%;
  margin: 0 auto;
  table-layout: fixed;
}

/* Table header styles */
.transactions-table .MuiTableCell-head {
  font-weight: 600;
  background-color: rgba(0, 0, 0, 0.03);
  padding: 12px 16px;
}

[data-theme="dark"] .transactions-table .MuiTableCell-head {
  background-color: rgba(255, 255, 255, 0.05);
  color: #ddd;
}

/* Table cells */
.transactions-table .MuiTableCell-body {
  padding: 12px 16px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Transaction type chip styles */
.transaction-type-chip {
  font-weight: 500 !important;
  min-width: 80px;
  justify-content: center;
}

.transaction-type-chip.income {
  background-color: rgba(46, 125, 50, 0.1) !important;
  color: #2e7d32 !important;
}

.transaction-type-chip.expense {
  background-color: rgba(211, 47, 47, 0.1) !important;
  color: #d32f2f !important;
}

.transaction-type-chip.transfer {
  background-color: rgba(25, 118, 210, 0.1) !important;
  color: #1976d2 !important;
}

/* Transaction amount styling */
.transaction-amount {
  font-weight: 600 !important;
  text-align: right !important;
}

.transaction-amount.income {
  color: #2e7d32 !important;
}

.transaction-amount.expense {
  color: #d32f2f !important;
}

.transaction-amount.transfer {
  color: #1976d2 !important;
}

/* Pagination styling */
.pagination-container {
  display: flex;
  justify-content: center;
  padding: 16px;
  background-color: rgba(0, 0, 0, 0.02);
  border-top: 1px solid rgba(0, 0, 0, 0.08);
}

[data-theme="dark"] .pagination-container {
  background-color: rgba(255, 255, 255, 0.03);
  border-top: 1px solid rgba(255, 255, 255, 0.08);
}

/* Category indicator in transactions */
.category-indicator {
  display: flex;
  align-items: center;
  gap: 8px;
}

.category-color-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  flex-shrink: 0;
}

/* Transaction page title container */
.page-title-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  width: 90%;
  max-width: 1000px;
  margin-right: auto;
  margin-left: 8%;
}

/* Transaction filter section */
.transaction-filters {
  display: flex;
  gap: 16px;
  margin-bottom: 24px;
  flex-wrap: wrap;
  width: 90%;
  max-width: 1000px;
  margin-right: auto;
  margin-left: 8%;
}

/* Responsive adjustments */
@media (max-width: 1024px) {
  .transactions-container,
  .page-title-container,
  .transaction-filters {
    width: 92%;
    margin-left: 6%;
  }
}

@media (max-width: 768px) {
  .transactions-container,
  .page-title-container,
  .transaction-filters {
    width: 95%;
    margin-left: 3%;
  }
  
  .transactions-table .MuiTableCell-head,
  .transactions-table .MuiTableCell-body {
    padding: 8px 12px;
  }
  
  .transaction-filters {
    flex-direction: column;
    gap: 12px;
  }
}

@media (max-width: 480px) {
  .transactions-container,
  .page-title-container,
  .transaction-filters {
    width: 98%;
    margin-left: 1%;
  }
  
  .transactions-table .MuiTableCell-head,
  .transactions-table .MuiTableCell-body {
    padding: 6px 8px;
    font-size: 0.8rem;
  }
}

/* Overwrite content-container for transactions page */
.content-container {
  align-items: flex-start;
}

/* Table cell widths for better consistency */
.transactions-table .date-cell {
  width: 10%;
}

.transactions-table .description-cell {
  width: 20%;
}

.transactions-table .category-cell,
.transactions-table .account-cell {
  width: 15%;
}

.transactions-table .amount-cell {
  width: 12%;
}

.transactions-table .type-cell {
  width: 10%;
}

.transactions-table .actions-cell {
  width: 8%;
} 